import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { debounceTime, Subject } from 'rxjs';

@Component({
  selector: 'lib-input-timer',
  standalone: true,
  imports: [
    FormsModule
  ],
  template: `
    <div class="position-relative">
      <a class="btn position-absolute end-0 border-0">
        <i class="fas fa-search"></i>
      </a>
      <input 
        class="form-control input" 
        type="search" 
        [placeholder]="placeholder" 
        [(ngModel)]="filtroTexto"
        (input)="onInput($event)">
    </div>
  `,
  styles: `
    .btn {
      cursor: text;
    }
  `
})
export class InputTimerComponent {

  public cambiosTexto: Subject<string> = new Subject<string>();

  @Input() public placeholder: string = '';
  @Input() public filtroTexto: string = '';
  @Output() public textoCompleto: EventEmitter<string> = new EventEmitter<string>();

  constructor() {
    this.cambiosTexto.pipe(debounceTime(500)).subscribe(value => {
      this.textoCompleto.emit(value);
    });
  }

  public onInput(event: any) {
    this.filtroTexto = event.target.value;
    this.cambiosTexto.next(this.filtroTexto);
  }

}
